* {
  box-sizing: border-box;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
}

body, html {
  font-family: "Quantico", sans-serif;
  font-size: 10px;
  color: #F8F8F8;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 400;
}

h1 {
  font-size: 3rem;
  color: #FD971F;
  font-weight: 700;
}

@media (min-width: 767px) {
  h1 {
    font-size: 4.8rem;
  }
}

h2 {
  font-size: 3rem;
}

@media (min-width: 767px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 2rem;
}

@media (min-width: 767px) {
  h3 {
    font-size: 2rem;
  }
}

h4 {
  font-size: 1.6rem;
}

@media (min-width: 767px) {
  h4 {
    font-size: 1.6rem;
  }
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

a {
  font-size: 1.6rem;
  color: inherit;
  text-decoration: none;
}

input {
  font-size: 1.6rem;
}

small, label {
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 1.5px;
}

body, html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
small,
span {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

article,
div,
footer,
header,
section,
li,
ul,
input,
nav,
form {
  box-sizing: border-box;
}

ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input {
  border: none;
  outline: none;
}

button {
  border: none;
  outline: 0;
}

.search__input {
  width: 100%;
  padding: 4rem 0;
}

.search__input input {
  border-radius: 3px;
  padding: 1.5rem;
  width: inherit;
}

.clusters:nth-child(-n+3) .cluster {
  padding-top: 0;
}

.clusters .cluster {
  padding: 3rem 1.5rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  border-left: 1px solid rgba(0, 0, 0, 0.12);
}

.clusters .cluster .cluster__name {
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
}

.clusters .cluster .shortcut {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 3px;
